<template>
	<div>
		<div class="box">
			<div class="item" v-on:mouseenter="show(0)" @mouseleave="leave(0)">
			  <!-- <div :class="{bgcolorblue:item.flag,bgcolor_gray:!item.flag}"></div>	 -->
			  <div :class="{show:true,move:shows[0],nomove:!shows[0]}">
				  <div class="cell">1</div>
				  <div class="cell">1</div>
				  <div class="cell">1</div>
				  <div class="cell">1</div>
				  <div class="cell">1</div>
				  <div class="cell">1</div>
			  </div>	
			</div>
			
			<div class="item" v-on:mouseenter="show(1)" @mouseleave="leave(1)">
			  <!-- <div :class="{bgcolorblue:item.flag,bgcolor_gray:!item.flag}"></div>	 -->
			  <div :class="{show:true,move:shows[1],nomove:!shows[1]}"></div>	
			</div>
			
			<div class="item" v-on:mouseenter="show(2)" @mouseleave="leave(2)">
			  <!-- <div :class="{bgcolorblue:item.flag,bgcolor_gray:!item.flag}"></div>	 -->
			  <div :class="{show:true,move:shows[2],nomove:!shows[2]}"></div>	
			</div>

		</div>
		<div class="box2">
			
			<div style="width: 50px;height: 200px; background: #000080;"></div>
			<div style="width: 50px;height: 200px; background: red;"></div>
			<div style="width: 50px;height: 200px; background: #000080;"></div>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				shows:[false,false,false]
			}
		},
		methods: {
			show(index) {
				
				//e.target.children[0].style="border: 3px solid blue;"
				//console.log(e.target.children[0].style)
				this.$set(this.shows, index,true);
				console.log(this.shows);
			},
			leave(index){
			
				this.$set(this.shows,index,false);
				//console.log(e)
				console.log(this.shows);
			}
		},
	}
</script>

<style>
	*{
		margin: 0px;
		padding: 0px;
		box-sizing: border-box;
	}
	
	html,body{
		/* background: #0040D0; */
	}
	.box{
		width: 600px;
		height: 300px;
		display: flex;
		border: 1px solid blue;
		margin-top: 100px;
		overflow: hidden;
		background: rgba(100,100,100,0.3);
	}
	.box .item{
		flex:1;
		margin-left: 5px;
		border: 1px solid red;
		
	}
	.box .item .show{
		width: 100%;
		height: 100%;
		background: #00FF00;
		transition: all 0.5s ;
		/* background: rgba(100,100,100,0.0); */
		display: flex;
		flex-flow: wrap;
		align-content: center;
		justify-content: center;
		align-items: center;
		
		
	}
	.nomove{
		transform: translateY(-101%);
	}
	.move{
		transform: translateY(0%);
	}
	.cell{
		width: 40%;
		height: 50px;
		background: red;
		margin: 5px 5px;
	}
	.box2{
		width: 500px;
		height: 400px;
		background: yellow;
		overflow-y: scroll;
	}
div::-webkit-scrollbar{
  width:5px;
  height:5px;
  /**/
}
div::-webkit-scrollbar-track{
  background: red;
  border-radius:2px;
}
div::-webkit-scrollbar-thumb{
  background: #bfbfbf;
  border-radius:10px;
}
div::-webkit-scrollbar-thumb:hover{
  background: #333;
}
div::-webkit-scrollbar-corner{
  background: #179a16;
}
</style>



