import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import shouye from '@/components/shouye'
import ziyuan from '@/components/ziyuan'
import test from '@/components/test'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'shouye',
      component: shouye
    },
	{
	  path: '/ziyuan',
	  name: 'ziyuan',
	  component: ziyuan
	},
	{
	  path: '/test',
	  name: 'test',
	  component: test
	}
  ]
})



